  deleteBlock(num) {
    var blocks = this.state.blocks;
    var index = blocks.map(function (obj) { return obj.num; }).indexOf(num);

    if (index === -1) {
      return false;
    }

    blocks.splice(index, 1);
    this.setState({ blocks: blocks });

    $('#heatBlock_' + (num - 1) + ' .block_delete').css('display', 'block');
  }

